<template>
  <v-app style="user-select: none">
    <v-main
      v-tap="rotate"
      v-tap:swipe.bottom="swipeDown"
      v-tap:swipe.top="swipeUp"
      v-tap:moving="moving"
      v-tap:start="start"
      v-tap:end="end"
      class="d-flex justify-center align-center indigo"
      style="touch-action: none"
    >
      
      <v-dialog v-model="showHowTo" :fullscreen="screenSize === 'xs'" max-width="500px">
        <v-card>
          <v-card-title>How To Play</v-card-title>
          <v-card-text>
            <p class="font-weight-bold">Goal</p>
            <p>
              The aim in Tetris is simple; you bring down blocks from the top of
              the screen. You can move the blocks around, either left to right
              and/or you can rotate them. The blocks fall at a certain rate, but
              you can make them fall faster if you're sure of your positioning.
            </p>
            <p class="font-weight-bold">Controls</p>
            <p>
              On a computer, you can use the arrow keys to move, rotate and
              place Tetrominos. Use the space bar to hard-drop your current
              Tetromino, and use shift to Hold a Tetromino for later use. By
              default, rotation achieved using the arrow keys is clockwise. Use
              the Control key to rotate pieces counter-clockwise.
            </p>
            <p>
              On a touchscreen device, drag to move a Tetromino left, right or down. Hard drops are achieved with a hard swipe down, and holds are achieved with a swipe up. Tap either the left or right side of your screen to rotate a Tetromino counter-clockwise and clockwise, respectively.
            </p>
          </v-card-text>
          <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="indigo dark darken-1"
            text
            @click="showHowTo = false"
          >
            OK
          </v-btn>
        </v-card-actions>
        </v-card>
      </v-dialog>
      <v-container v-if="gameOver" fluid>
        <v-row>
          <v-col cols="auto" class="d-flex flex-column ma-auto">
            <v-btn @click="[gameOver = false, gameStarted = true]">Play Tetris</v-btn>
            <v-btn @click="showHowTo = true" class="mt-3">How to Play</v-btn>
          </v-col>
        </v-row>
      </v-container>
      <v-container v-if="gameStarted" fluid class="py-0" style="touch-action: manipulation">
        <v-row
          class="justify-center space-between"
        >
          <v-col cols="auto" class="pa-0">
            <board
              @next-four="nextFour = $event"
              @on-hold="onHold = $event"
              @current-level="currentLevel = $event"
              @game-over="[gameOver = true, gameStarted = false]"
              :rotateCW="rotateCW"
              :rotateCCW="rotateCCW"
              :hardDrop="hardDrop"
              :holdPiece="holdPiece"
              :moveRight="moveRight"
              :moveLeft="moveLeft"
              :moveDown="moveDown"
            ></board>
          </v-col>
          <v-col
            cols="auto"
            class="d-flex flex-column justify-space-between py-0 pr-0"
          >
            <div>
              <tray :items="nextFour" :screenSize="screenSize"></tray>
              <tray
                :items="onHold ? [onHold] : onHold"
                :screenSize="screenSize"
                class="mt-3"
              ></tray>
            </div>
            <span class="white--text text-uppercase text-center"
              >Level {{ currentLevel }}</span
            >
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import Board from "./components/Board.vue";
import Tray from "./components/Tray.vue";

export default {
  name: "App",

  components: {
    Board,
    Tray,
  },

  data: () => ({
    gameStarted: false,
    gameOver: true,

    showHowTo: false,

    rotateCW: false,
    rotateCCW: false,
    hardDrop: false,
    holdPiece: false,
    moveRight: false,
    moveLeft: false,
    moveDown: false,

    benchmarkTouchCoords: undefined,
    consecutiveTouchEvents: 0,

    nextFour: undefined,
    onHold: undefined,
    currentLevel: 1,
  }),

  computed: {
    screenSize() {
      return this.$vuetify.breakpoint.name;
    },
    screenWidth() {
      return this.$vuetify.breakpoint.width;
    },
    cellSize() {
      return document.querySelector(".cell").offsetWidth;
    },
  },

  methods: {
    rotate(e) {
      if (this.gameStarted) {
        e.preventDefault();
      }

      let touch = e.changedTouches[0];

      if (touch.screenX > this.screenWidth / 2) {
        this.rotateCW = true;
        this.$nextTick(() => (this.rotateCW = false));
      } else {
        this.rotateCCW = true;
        this.$nextTick(() => (this.rotateCCW = false));
      }
    },
    swipeDown() {
      if (this.consecutiveTouchEvents < 12) {
        this.hardDrop = true;
        this.$nextTick(() => (this.hardDrop = false));
      }
    },
    swipeUp() {
      this.holdPiece = true;
      this.$nextTick(() => (this.holdPiece = false));
    },
    moving(e) {
      this.consecutiveTouchEvents++;

      let [benchmarkX, benchmarkY] = this.benchmarkTouchCoords;
      let xPos = e.changedTouches[0].pageX;
      let yPos = e.changedTouches[0].pageY;

      if (benchmarkX - xPos < -this.cellSize) {
        this.benchmarkTouchCoords = [xPos, yPos];
        this.moveRight = true;

        this.$nextTick(() => (this.moveRight = false));
      } else if (benchmarkX - xPos > this.cellSize) {
        this.benchmarkTouchCoords = [xPos, yPos];
        this.moveLeft = true;

        this.$nextTick(() => (this.moveLeft = false));
      } else if (benchmarkY - yPos < -this.cellSize) {
        this.benchmarkTouchCoords = [xPos, yPos];
        this.moveDown = true;

        this.$nextTick(() => (this.moveDown = false));
      }

      return;
    },
    start(e) {
      let xPos = e.changedTouches[0].pageX;
      let yPos = e.changedTouches[0].pageY;

      return (this.benchmarkTouchCoords = [xPos, yPos]);
    },
    end() {
      this.$nextTick(() => {
        this.consecutiveTouchEvents = 0;
        this.benchmarkTouchCoords = undefined;
      });
    },
  },
};
</script>